




















	import server from '../../store/server.js';
	export default {
		data() {
			return {
				messageDatalist: [
					{
						"type":19,          
						"contents":"混凝土浇筑令签字",       
						"count":1,       //上传人ID
					},
					{
						"type":19,             
						"contents":"混凝土浇筑令签字",       
						"count":1,       
					}
				],
			}
		},
		methods: {
			getMessagelist() {
				let url = server.net.setUrl('api/NoticeNewsApi/AppGetNotify');
				
				uni.request({
					url: url,
					method: 'GET',
					header: {
						Token: server.net.GetSessionToken()
					},
					success: (res) => {
						console.log(res)
 						let jsondata = res.data;
 						let messArr=jsondata.data;
						
						this.messageDatalist = messArr;
						uni.stopPullDownRefresh();
					},
					fail: (f) => {
						
					},
					complete: (c) => {
						
					}
				});
				
			},
			getDetailList(infoType){
				switch(infoType)
				{
					case 1:
					    break;
						uni.navigateTo({
							url: '../index/quality/QualityList?infoType='+infoType +'&state=3'
						});
						break;
					case 18:
					    uni.navigateTo({
					    	url: '../index/schedule/scheduleList?state=all&notify=true'
					    });
					    break;
					case 19:
					    uni.navigateTo({
					    	url: '../index/site/ConcretePourList?notify=true'
					    });
					    break;
					
				}
				
			}
		},
		onLoad(option) {
			//根据页面传值判断页面内容：检查记录&整改记录，并动态设置页面标题
			//获取文件列表
			this.getMessagelist();
			
		},onPullDownRefresh() {
			console.log('下拉刷新');
			this.getMessagelist();
		}
	}



